import React, { Component } from 'react';
import './App.css';
import img_elBitmap from './images/StevenUndeConstructionScreen_elBitmap.jpg';
import img_elIcone from './images/StevenUndeConstructionScreen_elIcone.png';
import img_elBitmap2 from './images/StevenUndeConstructionScreen_elBitmap2.png';
import img_elBitmap3 from './images/StevenUndeConstructionScreen_elBitmap3.png';
import img_elBitmap4 from './images/StevenUndeConstructionScreen_elBitmap4.png';

// UI framework component imports
import Container from 'muicss/lib/react/container';


export default class StevenUndeConstructionScreen extends Component {

  constructor(props) {
    super(props);
    
    this.state = {
      stefan: (<div>STEFAN</div>),
      stefan_plainText: "STEFAN",
      burcea: (<div>BURCEA</div>),
      burcea_plainText: "BURCEA",
      n004: (<div>004 0734520713 </div>),
      n004_plainText: "004 0734520713 ",
      stefanfburceagmai: (<div>stefan.f.burcea@gmail.com</div>),
      stefanfburceagmai_plainText: "stefan.f.burcea@gmail.com",
      aboutMe: (<div>ABOUT ME </div>),
      aboutMe_plainText: "ABOUT ME ",
      work: (<div>WORK </div>),
      work_plainText: "WORK ",
    };
  }

  // Properties used by this component:
  // appActions, deviceInfo

  onClick_WORK = (ev) => {
    // Go to screen 'steven unde construction 2'
    this.props.appActions.goToScreen('stevenundeconstruction2', { transitionId: '_default' });
  
  }
  
  
  onClick_bitmap3 = (ev) => {
    // Go to screen 'steven unde construction 3'
    this.props.appActions.goToScreen('stevenundeconstruction3', { transitionId: '_default' });
  
  }
  
  
  render() {
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
  
    const style_bitmap = {
        height: 'auto',
        pointerEvents: 'none',
     };
    const style_icone = {
        background: 'url('+img_elIcone+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_STEFAN = {
        fontSize: 19.0,
        fontFamily: "'Roboto-Black', sans-serif",
        color: 'black',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_BURCEA = {
        fontSize: 19.0,
        fontFamily: "'Roboto-Black', sans-serif",
        color: 'black',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_n004 = {
        fontSize: 12.3,
        fontFamily: "'Roboto-Medium', sans-serif",
        color: 'black',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_stefanfburceagmai = {
        fontSize: 12.3,
        fontFamily: "'Roboto-Medium', sans-serif",
        color: 'black',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_ABOUTME = {
        fontSize: 19.0,
        fontFamily: "'HelveticaNeue-Medium', sans-serif",
        color: 'black',
        textAlign: 'left',
        pointerEvents: 'none',
     };
    const style_WORK = {
        fontSize: 19.0,
        fontFamily: "'HelveticaNeue-Medium', sans-serif",
        color: 'black',
        textAlign: 'left',
        cursor: 'pointer',
     };
    const style_bitmap2 = {
        height: 'auto',
        pointerEvents: 'none',
     };
    const style_bitmap3 = {
        height: 'auto',
        cursor: 'pointer',
     };
    const style_bitmap4 = {
        height: 'auto',
        pointerEvents: 'none',
     };
    
    return (
      <Container fluid={true} className="AppScreen StevenUndeConstructionScreen" style={baseStyle}>
        <div className="screenFgContainer">
          <div className="foreground">
            <img className='containerMinHeight elBitmap' style={style_bitmap} src={img_elBitmap} alt=""  />
            <div className='elIcone' style={style_icone} />
            <div className='elSTEFAN' style={style_STEFAN}>
              <div>{this.state.stefan}</div>
            </div>
            <div className='elBURCEA' style={style_BURCEA}>
              <div>{this.state.burcea}</div>
            </div>
            <div className='elN004' style={style_n004}>
              <div>{this.state.n004}</div>
            </div>
            <div className='elStefanfburceagmai' style={style_stefanfburceagmai}>
              <div>{this.state.stefanfburceagmai}</div>
            </div>
            <div className='elABOUTME' style={style_ABOUTME}>
              <div>{this.state.aboutMe}</div>
            </div>
            <div className='elWORK' style={style_WORK} onClick={this.onClick_WORK} >
              <div>{this.state.work}</div>
            </div>
            <img className='elBitmap2' style={style_bitmap2} src={img_elBitmap2} alt=""  />
            <img className='elBitmap3' style={style_bitmap3} src={img_elBitmap3} alt="" onClick={this.onClick_bitmap3}  />
            <img className='elBitmap4' style={style_bitmap4} src={img_elBitmap4} alt=""  />
          </div>
        </div>
      </Container>
    )
  }

}



