import React, { Component } from 'react';
import './App.css';
import img_elBitmap from './images/StevenUndeConstruction3Screen_elBitmap.jpg';
import img_elIcone from './images/StevenUndeConstruction3Screen_elIcone.png';
import img_elSTEFAN from './images/StevenUndeConstruction3Screen_elSTEFAN.png';
import img_elBURCEA from './images/StevenUndeConstruction3Screen_elBURCEA.png';
import img_elN004 from './images/StevenUndeConstruction3Screen_elN004.png';
import img_elStefanfburceagmai from './images/StevenUndeConstruction3Screen_elStefanfburceagmai.png';
import img_elABOUTME from './images/StevenUndeConstruction3Screen_elABOUTME.png';
import img_elWORK from './images/StevenUndeConstruction3Screen_elWORK.png';
import img_elBitmap2 from './images/StevenUndeConstructionScreen_elBitmap3.png';

// UI framework component imports
import Container from 'muicss/lib/react/container';


export default class StevenUndeConstruction3Screen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  onClick_ABOUTME = (ev) => {
    // Go to screen 'steven unde construction'
    this.props.appActions.goToScreen('stevenundeconstruction', { transitionId: '_default' });
  
  }
  
  
  onClick_WORK = (ev) => {
    // Go to screen 'steven unde construction 2'
    this.props.appActions.goToScreen('stevenundeconstruction2', { transitionId: '_default' });
  
  }
  
  
  render() {
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
  
    
    let contentElement_elSwapper;  // This element's content can vary based on screen size
    
    const style_bitmap = {
        height: 'auto',
        pointerEvents: 'none',
     };
    const style_icone = {
        background: 'url('+img_elIcone+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_STEFAN = {
        background: 'url('+img_elSTEFAN+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_BURCEA = {
        background: 'url('+img_elBURCEA+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_n004 = {
        background: 'url('+img_elN004+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_stefanfburceagmai = {
        background: 'url('+img_elStefanfburceagmai+')',
        backgroundSize: '100% 100%',
        pointerEvents: 'none',
     };
    const style_ABOUTME = {
        background: 'url('+img_elABOUTME+')',
        backgroundSize: '100% 100%',
        cursor: 'pointer',
     };
    const style_WORK = {
        background: 'url('+img_elWORK+')',
        backgroundSize: '100% 100%',
        cursor: 'pointer',
     };
    const style_bitmap2 = {
        height: 'auto',
        pointerEvents: 'none',
     };
    
    return (
      <Container fluid={true} className="AppScreen StevenUndeConstruction3Screen" style={baseStyle}>
        <div className="layoutFlow">
          <div className='hasNestedComps elSwapper'>
            <div>
              {contentElement_elSwapper}
            </div>
          
          </div>
        </div>
        <div className="screenFgContainer">
          <div className="foreground">
            <img className='containerMinHeight elBitmap' style={style_bitmap} src={img_elBitmap} alt=""  />
            <div className='elIcone' style={style_icone} />
            <div className='elSTEFAN' style={style_STEFAN} />
            <div className='elBURCEA' style={style_BURCEA} />
            <div className='elN004' style={style_n004} />
            <div className='elStefanfburceagmai' style={style_stefanfburceagmai} />
            <div className='elABOUTME' style={style_ABOUTME} onClick={this.onClick_ABOUTME}  />
            <div className='elWORK' style={style_WORK} onClick={this.onClick_WORK}  />
            <img className='elBitmap2' style={style_bitmap2} src={img_elBitmap2} alt=""  />
          </div>
        </div>
      </Container>
    )
  }

}



